<style>
	.v-select {
		position: relative;
	}

	.v-select .open-indicator {
		position: absolute;
		bottom: 6px;
		right: 10px;
		display: inline-block;
		cursor: pointer;
		pointer-events: all;
		transition: all 150ms cubic-bezier(1.000, -0.115, 0.975, 0.855);
		transition-timing-function: cubic-bezier(1.000, -0.115, 0.975, 0.855);
		opacity: 1;
		transition: opacity .1s;
	}

	.v-select.loading .open-indicator {
		opacity: 0;
	}

	.v-select .open-indicator:before {
		border-color: rgba(60, 60, 60, .5);
		border-style: solid;
		border-width: 0.25em 0.25em 0 0;
		content: '';
		display: inline-block;
		height: 10px;
		width: 10px;
		vertical-align: top;
		transform: rotate(133deg);
		transition: all 150ms cubic-bezier(1.000, -0.115, 0.975, 0.855);
		transition-timing-function: cubic-bezier(1.000, -0.115, 0.975, 0.855);
	}

	.v-select.open .open-indicator {
		bottom: 1px;
	}

	.v-select.open .open-indicator:before {
		transform: rotate(315deg);
	}

	.v-select .dropdown-toggle {
		display: block;
		padding: 0;
		background: none;
		border: 1px solid rgba(60, 60, 60, .26);
		border-radius: 4px;
		white-space: normal;
	}

	.v-select.searchable .dropdown-toggle {
		cursor: text;
	}

	.v-select.open .dropdown-toggle {
		border-bottom: none;
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
	}

	.v-select > .dropdown-menu {
		margin: 0;
		width: 100%;
		overflow-y: auto;
		border-top: none;
		border-top-left-radius: 0;
		border-top-right-radius: 0;
	}

	.v-select .selected-tag {
		color: #333;
		margin: 4px 1px 0px 3px;
		padding: 0 0.25em;
		float: left;
		line-height: 1.7em;
	}

	.v-select .selected-tag.multiple {
		background-color: #f0f0f0;
		border: 1px solid #ccc;
		border-radius: 4px;
	}

	.v-select .selected-tag .close {
		float: none;
		margin-right: 0;
	}

	.v-select input[type=search],
	.v-select input[type=search]:focus {
		display: inline-block;
		border: none;
		outline: none;
		margin: 0;
		padding: 0 .5em;
		width: 10em;
		max-width: 100%;
		background: none;
		position: relative;
		box-shadow: none;
		float: left;
		clear: none;
	}

	.v-select input[type=search]:disabled {
		cursor: pointer;
	}

	.v-select li a {
		cursor: pointer;
	}

	.v-select .active a {
		background: rgba(50, 50, 50, .1);
		color: #333;
	}

	.v-select .highlight a,
	.v-select li:hover > a {
		background: #f0f0f0;
		color: #333;
	}

	.v-select .spinner {
		position: absolute;
		top: 1em;
    right: 4em;
		font-size: 5px;
		text-indent: -9999em;
		overflow: hidden;
		border-top: .9em solid rgba(100, 100, 100, .1);
		border-right: .9em solid rgba(100, 100, 100, .1);
		border-bottom: .9em solid rgba(100, 100, 100, .1);
		border-left: .9em solid rgba(60, 60, 60, .45);
		transform: translateZ(0);
		animation: vSelectSpinner 1.1s infinite linear;
		transition: opacity .1s;
	}

	.v-select .spinner,
	.v-select .spinner:after {
		border-radius: 50%;
		width: 4em;
		height: 4em;
	}

	@-webkit-keyframes vSelectSpinner {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}

	@keyframes vSelectSpinner {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
</style>

<template>
	<div class="dropdown v-select" :class="dropdownClasses">
		<div ref="toggle" @mousedown.prevent="toggleDropdown" class="dropdown-toggle clearfix" type="button">

        <span :class="selectedTagClasses" v-for="option in selectedOptions" v-bind:key="option.index">
					<slot name="selected" :data="getOptionData(option)" :label="getOptionLabel(option)" :value="getOptionValue(option)">
						{{ getOptionLabel(option) }}
					</slot>
          <button @click="select(option)" type="button" class="close">
            <span aria-hidden="true">&times;</span>
          </button>
        </span>

			<input
							ref="search"
							:debounce="debounce"
							v-model="search"
							@keydown.delete="maybeDeleteValue"
							@keyup.esc="onEscape"
							@keydown.up.prevent="typeAheadUp"
							@keydown.down.prevent="typeAheadDown"
							@keyup.enter.prevent="typeAheadSelect"
							@blur="open = false"
							@focus="open = true"
							type="search"
							class="form-control"
							:placeholder="searchPlaceholder"
							:readonly="!searchable"
							:style="{ width: isValueEmpty ? '100%' : 'auto' }"
			>

			<slot name="open-indicator">
				<i ref="openIndicator" role="presentation" class="open-indicator"></i>
			</slot>

			<slot name="spinner">
				<div class="spinner" v-show="mutableLoading">Loading...</div>
			</slot>
		</div>

		<ul ref="dropdownMenu" v-show="open" :transition="transition" class="dropdown-menu" :style="{ 'max-height': maxHeight }" @scroll="scroll">
			<li v-for="(option, index) in filteredOptions" v-bind:key="index" :class="{ active: isOptionSelected(option), highlight: index === typeAheadPointer }" @mouseover="typeAheadPointer = index">
				<a @mousedown.prevent="select(option)">
					<slot name="item"  :data="getOptionData(option)" :label="getOptionLabel(option)" :value="getOptionValue(option)">
						{{ getOptionLabel(option) }}
					</slot>
				</a>
			</li>
			<transition name="fade">
				<li v-if="!filteredOptions.length" class="divider"></li>
			</transition>
			<transition name="fade">
				<li v-if="!filteredOptions.length" class="text-center">
					<slot name="no-options">Sorry, no matching options.</slot>
				</li>
			</transition>
		</ul>
	</div>
</template>


<script type="text/babel">
	import pointerScroll from '../mixins/pointerScroll'
	import typeAheadPointer from '../mixins/typeAheadPointer'
	import ajax from '../mixins/ajax'

	export default {
		mixins: [pointerScroll, typeAheadPointer, ajax],

		props: {
			/**
			 * Contains the currently selected value. Very similar to a
			 * `value` attribute on an <input>. You can listen for changes
			 * using 'change' event using v-on
			 * @type {Object||String||null}
			 */
			value: {
				default: null
			},

			/**
			 * An array of strings or objects to be used as dropdown choices.
			 * If you are using an array of objects, vue-select will look for
			 * a `label` key (ex. [{label: 'This is Foo', value: 'foo'}]). A
			 * custom label key can be set with the `label` prop.
			 * @type {Object}
			 */
			options: {
				type: Array,
				default() {
					return []
				},
			},

			/**
			 * Sets the max-height property on the dropdown list.
			 * @deprecated
			 * @type {String}
			 */
			maxHeight: {
				type: String,
				default: '400px'
			},

			/**
			 * Accept a callback function that will be
			 * run when the dropdown has a scrollbar and
			 * is scrolled to the bottom.
			 */
			onScrollEnd: {
				type: Function,
				default: function(){}
			},

			/**
			 * Enable/disable filtering the options.
			 * @type {Boolean}
			 */
			searchable: {
				type: Boolean,
				default: true
			},

			/**
			 * Equivalent to the `multiple` attribute on a `<select>` input.
			 * @type {Object}
			 */
			multiple: {
				type: Boolean,
				default: false
			},

			/**
			 * Equivalent to the `placeholder` attribute on an `<input>`.
			 * @type {Object}
			 */
			placeholder: {
				type: String,
				default: ''
			},

			/**
			 * Sets a Vue transition property on the `.dropdown-menu`. vue-select
			 * does not include CSS for transitions, you'll need to add them yourself.
			 * @type {String}
			 */
			transition: {
				type: String,
				default: 'expand'
			},

			/**
			 * Enables/disables clearing the search text when an option is selected.
			 * @type {Boolean}
			 */
			clearSearchOnSelect: {
				type: Boolean,
				default: true
			},

			/**
			 * Tells vue-select what key to use when generating option
			 * labels when each `option` is an object.
			 * @type {String}
			 */
			label: {
				type: String,
				default: 'label'
			},

			/**
			 * Callback to generate the label text. If {option}
			 * is an object, returns option[this.label] by default.
			 * @param  {Object || String} option
			 * @return {String}
			 */
			getOptionLabel: {
				type: Function,
				default(option) {
					if (typeof option === 'object') {
						if (this.label && option[this.label]) {
							return option[this.label]
						}
					}
					return option;
				}
			},

			/**
			 * Callback to generate the value text. If {option}
			 * is an object, returns option[this.value] by default.
			 * @param  {Object || String} option
			 * @return {String}
			 */
			getOptionValue: {
				type: Function,
				default(option) {
					if (typeof option === 'object') {
						if (option.value) {
							return option.value
						}
					}
					return option;
				}
			},

			/**
			 * Callback to extract additional data from option.
			 * @param  {Object || String} option
			 * @return {Object}
			 */
			getOptionData: {
				type: Function,
				default(option) {
					if (typeof option === 'object') {
						if (option.data) {
							return option.data
						}
					}
					return {};
				}
			},

			/**
			 * An optional callback function that is called each time the selected
			 * value(s) change. When integrating with Vuex, use this callback to trigger
			 * an action, rather than using :value.sync to retreive the selected value.
			 * @type {Function}
			 * @default {null}
			 */
			onChange: {
				type: Function,
				default: function(val) {
					this.$emit('input', val)
				}
			},

			/**
			 * Enable/disable creating options from searchInput.
			 * @type {Boolean}
			 */
			taggable: {
				type: Boolean,
				default: false
			},

			/**
			 * When true, newly created tags will be added to
			 * the options list.
			 * @type {Boolean}
			 */
			pushTags: {
				type: Boolean,
				default: false
			},

			/**
			 * User defined function for adding Options
			 * @type {Function}
			 */
			createOption: {
				type: Function,
				default: function (newOption) {
					if (typeof this.mutableOptions[0] === 'object') {
						return {[this.label]: newOption}
					}
					return newOption
				}
			},

			/**
			 * When false, updating the options will not reset the select value
			 * @type {Boolean}
			 */
			resetOnOptionsChange: {
				type: Boolean,
				default: false
			},
		},

		data() {
			return {
				search: '',
				open: false,
				mutableValue: null,
				mutableOptions: [],
				mutableLoading: false
			}
		},

		watch: {
			/**
			 * When the value prop changes, update
			 * the internal mutableValue.
			 * @param  {mixed} val
			 * @return {void}
			 */
			value(val) {
				console.log('watch value', val)
				this.mutableValue = val
			},

			/**
			 * Maybe run the onChange callback.
			 * @param  {string|object} val
			 * @param  {string|object} old
			 * @return {void}
			 */
			mutableValue(val, old) {
				if (this.multiple) {
					this.onChange ? this.onChange(val) : null
				} else {
					this.onChange && val !== old ? this.onChange(val) : null
				}
			},

			/**
			 * When options change, update
			 * the internal mutableOptions.
			 * @param  {array} val
			 * @return {void}
			 */
			options(val) {
				this.mutableOptions = val
			},

			/**
			 * Maybe reset the mutableValue
		 	 * when mutableOptions change.
			 * @return {[type]} [description]
			 */
			mutableOptions() {
				if (!this.taggable && this.resetOnOptionsChange) {
					this.mutableValue = this.multiple ? [] : null
				}
			},

			/**
			 * Always reset the mutableValue when
			 * the multiple prop changes.
			 * @param  {Boolean} val
			 * @return {void}
			 */
			multiple(val) {
				this.mutableValue = val ? [] : null
 			}
		},

		created() {
			this.mutableValue = this.value
			this.mutableOptions = this.options.slice(0)
			this.mutableLoading = this.loading
		},

		methods: {

			/**
			 * Select a given option.
			 * @param  {Object|String} option
			 * @return {void}
			 */
			select(option) {
				if (this.isOptionSelected(option)) {
					this.deselect(option)
				} else if (this.optionExists(option)){
					if (this.multiple) {
						if (!this.mutableValue) {
							this.mutableValue = [option]
						} else {
							this.mutableValue.push(option)
						}
					} else {
						this.mutableValue = option
					}
					this.onAfterSelect(option)
				} else if (this.taggable) {
					option = this.createOption(option)
					if (this.pushTags) {
						this.mutableOptions.push(option)
					}
					this.onAfterSelect(option)
				}
			},

			/**
			 * De-select a given option.
			 * @param  {Object|String} option
			 * @return {void}
			 */
			deselect(option) {
				if (this.multiple) {
					let ref = -1
					this.mutableValue.forEach(val => {
						if (val === option || typeof val === 'object' && val[this.label] === option[this.label]) {
							ref = val
						}
					})
					var index = this.mutableValue.indexOf(ref)
					this.mutableValue.splice(index, 1)
				} else {
					this.mutableValue = null
				}
			},

			/**
			 * Called from this.select after each selection.
			 * @param  {Object|String} option
			 * @return {void}
			 */
			onAfterSelect(option) {
				if (!this.multiple) {
					this.open = false
					this.$refs.search.blur()
				}

				if (this.clearSearchOnSelect) {
					this.search = ''
				}
			},

			/**
			 * Handles dropdown scroll. Calls onScrollEnd when
			 * the dropdown is scrollable and scrolled to the
			 * bottom.
			 */
			scroll(event) {
        var elem = event.currentTarget;
				// console.log(elem.scrollHeight, elem.scrollTop, elem.offsetHeight)
        if (elem.scrollHeight - elem.scrollTop <= elem.offsetHeight) {
					if (typeof this.onScrollEnd === 'function') {
						this.onScrollEnd()
					}
        }
			},

			/**
			 * Toggle the visibility of the dropdown menu.
			 * @param  {Event} e
			 * @return {void}
			 */
			toggleDropdown(e) {
				if (e.target === this.$refs.openIndicator || e.target === this.$refs.search || e.target === this.$refs.toggle || e.target === this.$el) {
					if (this.open) {
						this.$refs.search.blur() // dropdown will close on blur
					} else {
						this.open = true
						this.$refs.search.focus()
						// SEARCH NOW
					}
				}
			},

			/**
			 * Check if the given option is currently selected.
			 * @param  {Object|String}  option
			 * @return {Boolean}        True when selected | False otherwise
			 */
			isOptionSelected(option) {
				if (this.multiple && this.mutableValue) {
					let selected = false
					this.mutableValue.forEach(opt => {
						if (typeof opt === 'object' && opt[this.label] === option[this.label]) {
							selected = true
						} else if (opt === option) {
							selected = true
						}
					})
					return selected
				}

				if (option && this.mutableValue === option.value) return true
				return this.mutableValue === option
			},

			/**
			 * If there is any text in the search input, remove it.
			 * Otherwise, blur the search input to close the dropdown.
			 * @return {[type]} [description]
			 */
			onEscape() {
				if (!this.search.length) {
					this.$refs.search.blur()
				} else {
					this.search = ''
				}
			},

			/**
			 * Delete the value on Delete keypress when there is no
			 * text in the search input, & there's tags to delete
			 * @return {this.value}
			 */
			maybeDeleteValue() {
				if (!this.$refs.search.value.length && this.mutableValue) {
					return this.multiple ? this.mutableValue.pop() : this.mutableValue = null
				}
			},

			/**
			 * Determine if an option exists
			 * within this.mutableOptions array.
			 *
			 * @param  {Object || String} option
			 * @return {boolean}
			 */
			optionExists(option) {
				let exists = false

				this.mutableOptions.forEach(opt => {
					if (typeof opt === 'object' && opt[this.label] === option) {
						exists = true
					} else if (opt === option) {
						exists = true
					}
				})

				return exists
			}
		},

		computed: {

		/**
		 * Classes to be output on selected tags
		 * @return {Object}
		 */
			selectedTagClasses() {
				return {
					'selected-tag': true,
					multiple: this.multiple
				}
			},

			/**
			 * Classes to be output on .dropdown
			 * @return {Object}
			 */
			dropdownClasses() {
				return {
					open: this.open,
					searchable: this.searchable,
					loading: this.mutableLoading
				}
			},

			/**
			 * Return the placeholder string if it's set
			 * & there is no value selected.
			 * @return {String} Placeholder text
			 */
			searchPlaceholder() {
				if (this.isValueEmpty && this.placeholder) {
					return this.placeholder;
				}
			},

			/**
			 * The currently displayed options, filtered
			 * by the search elements value. If tagging
			 * true, the search text will be prepended
			 * if it doesn't already exist.
			 *
			 * @return {array}
			 */
			filteredOptions() {
				let search = this.search.toLowerCase()
				let options = this.mutableOptions.filter((option) => {
					if( typeof option === 'object' ) {
						return option[this.label].toLowerCase().indexOf(search) > -1
					}
					return option.toLowerCase().indexOf(search) > -1
				})
				if (this.taggable && search.length && !this.optionExists(search)) {
					options.unshift(search)
				}
				return options
			},

			selectedOptions() {
				return this.mutableOptions.filter(this.isOptionSelected, this)
			},

			/**
			 * Check if there aren't any options selected.
			 * @return {Boolean}
			 */
			isValueEmpty() {
				if (this.mutableValue === null || this.mutableValue === undefined) {
					return true;
				}
				if (typeof this.mutableValue === 'object') {
					return !Object.keys(this.mutableValue).length
				}
				if (typeof this.mutableValue === 'string') {
					return !this.mutableValue.trim().length
				}
				return false;
			},
		},

	}
</script>



